/**
 * command.js
 * This file handles the logic for the Outlook Add-in commands,
 * specifically fetching email and attachment data and sending it
 * to a dialog window (React app).
 */
(function () {
  "use strict";

  // Utility function to convert a Blob object to a Base64 string.
  // Returns a Promise that resolves with the base64 string (without the data: prefix).
  function blobToBase64(blob) {
      return new Promise((resolve, reject) => {
          const reader:any = new FileReader();
          reader.onerror = (error) => {
              console.error("FileReader Error:", error);
              reject(new Error("Failed to read blob for base64 conversion."));
          };
          reader.onload = () => {
              // reader.result contains the base64 data URL (e.g., "data:mime/type;base64,...")
              // We split to get only the base64 part after the comma.
              try {
                  const base64String = reader.result.split(',')[1];
                  if (!base64String) {
                       // Handle cases where split might fail (e.g., empty blob)
                       console.warn("Base64 conversion resulted in empty string.");
                       resolve(""); // Resolve with empty string for empty blobs
                  } else {
                      resolve(base64String);
                  }
              } catch (e) {
                  console.error("Error processing FileReader result:", e);
                  reject(new Error("Error extracting base64 string from FileReader result."));
              }
          };
          // Start reading the blob as a Data URL.
          reader.readAsDataURL(blob);
      });
  }

  // The initialize function must run every time the add-in context loads.
  Office.initialize = function (reason) {
      // You can add initialization logic here if needed.
      // 'reason' indicates why initialize was called (e.g., Office.InitializationReason.Inserted)
      console.log(`Office.initialize called. Reason: ${reason}`);
  };

  // Helper function to get the REST-formatted ID of the current mailbox item.
  function getItemRestId() {
      // Check if the host is Outlook for iOS, which uses EWS IDs directly.
      if (Office.context.mailbox.diagnostics.hostName === 'OutlookIOS') {
          console.log("Host is OutlookIOS, returning EWS Item ID.");
          return Office.context.mailbox.item.itemId;
      } else {
          // For other clients (Web, Desktop), convert the EWS ID to a REST ID.
          console.log("Host is not OutlookIOS, converting EWS ID to REST ID v2.0.");
          try {
               return Office.context.mailbox.convertToRestId(
                  Office.context.mailbox.item.itemId,
                  Office.MailboxEnums.RestVersion.v2_0
               );
          } catch(e) {
               console.error("Error converting Item ID to REST ID:", e);
               // Fallback or throw, depending on requirements
               throw new Error("Could not convert Item ID to REST ID.");
          }

      }
  }

  // Asynchronous function to fetch the entire email (.eml format) as a Blob.
  // Returns a Promise that resolves with the Blob object.
  async function fetchEmlBlob() {
      console.log("Attempting to fetch .eml blob...");
      return new Promise((resolve, reject) => {
          // Get an authentication token for making REST API calls.
          Office.context.mailbox.getCallbackTokenAsync({ isRest: true }, function (result) {
              if (result.status !== Office.AsyncResultStatus.Succeeded) {
                  console.error("Error getting REST callback token:", result.error);
                  reject(new Error("Failed to get REST callback token: " + result.error?.message));
                  return;
              }
              console.log("Successfully obtained REST callback token.");
              const token = result.value;
              const restItemId = getItemRestId(); // Get the correct ID format
              const getMessageUrl = `${Office.context.mailbox.restUrl}/v2.0/me/messages/${restItemId}/$value`;
              console.log(`Fetching from URL: ${getMessageUrl}`);

              // Use the fetch API to get the raw MIME content of the message.
              fetch(getMessageUrl, {
                  method: "GET",
                  headers: new Headers({
                      "Authorization": `Bearer ${token}`,
                       "Accept": "application/octet-stream" // Explicitly ask for stream/blob
                  })
              })
              .then(function (response) {
                  console.log(`Fetch response status: ${response.status} ${response.statusText}`);
                  if (!response.ok) {
                      // Attempt to read error response body if available
                      return response.text().then(body => {
                           console.error("Fetch Error Body:", body);
                          throw new Error(`Error fetching .eml message (${response.status}): ${response.statusText}. ${body}`);
                      });
                  }
                  // If response is OK, get the response body as a Blob.
                  return response.blob();
              })
              .then(function (blob) {
                  console.log(`Successfully fetched .eml blob, size: ${blob?.size}, type: ${blob?.type}`);
                  resolve(blob); // Resolve the promise with the Blob object.
              })
              .catch(function (error) {
                  console.error("Fetch .eml error:", error);
                  reject(error); // Reject the promise if any error occurs during fetch.
              });
          });
      });
  };

  // Main function called by the Outlook event (e.g., OnSend) or action.
  async function validateBeforeSend(event) {
      console.log("--- validateBeforeSend started ---");
      const item = Office.context.mailbox.item;
      let dialog = null; // Keep track of the dialog instance

      try {
          // 1. Fetch the email content as a blob. This must succeed first.
          console.log("Step 1: Fetching .eml content...");
          const emailBlob = await fetchEmlBlob(); // Fetch and wait for the blob

           // Check if blob has size, otherwise fetch might have failed silently
           if (!emailBlob || emailBlob==null) {
              throw new Error("Fetched email blob is invalid or empty.");
          }
          console.log("Step 1: Fetching .eml content successful.");

          // 2. Open the dialog window where the React app runs.
          console.log("Step 2: Opening dialog...");
          await new Promise<void>((resolveDialog, rejectDialog) => {
              Office.context.ui.displayDialogAsync(
                  "https://shahzadumar-w.github.io/Boligflow_Outlook_integration/ReactDialog.html", // Make sure this URL is correct and HTTPS
                  { height: 75, width: 50, displayInIframe: true }, // Recommended settings
                  function (asyncResult) {
                      if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
                          dialog = asyncResult.value; // Store the dialog instance
                          console.log("Step 2: Dialog opened successfully.");

                          // 3. Setup handlers for messages FROM the dialog and for dialog errors.
                          console.log("Step 3: Setting up dialog event handlers...");

                           // Handler for errors originating within the dialog window itself.
                          dialog.addEventHandler(Office.EventType.DialogEventReceived, (args) => {
                              console.error(`Dialog Event Error: Code=${args.error}, Message='${args.message || 'N/A'}'`);
                              // Depending on the error, you might want to fail the event.
                              // For critical errors (like code 12006 - Dialog Closed by User), we should probably stop.
                               if (args.error === 12006) {
                                   console.log("Dialog closed by user or navigated away.");
                                   if(event && typeof event.completed === 'function'){
                                        event.completed({ allowEvent: false, errorMessage: "Operation cancelled." });
                                   }
                              } else {
                                  // Handle other dialog errors (12002, 12003, 12007 etc.)
                                  // Optionally: event.completed({ allowEvent: false, errorMessage: "A dialog error occurred." });
                              }
                              try { if (dialog) dialog.close(); } catch (e) {} // Attempt to close if still open
                          });


                          // Handler for messages sent FROM the React dialog using messageParent.
                          dialog.addEventHandler(Office.EventType.DialogMessageReceived, async (args) => {
                              console.log(`Message received from dialog: ${args.message}`);

                              // Check if the React dialog is requesting the email/attachment data.
                              if (args.message === 'data_request') {
                                  console.log("Processing 'data_request'...");
                                  try {
                                      // 4. Fetch attachment content (base64) *after* request received.
                                      console.log("Step 4: Fetching attachment content...");
                                      let attachmentsWithContent = [];
                                      if (item.attachments && item.attachments.length > 0) {
                                            attachmentsWithContent = await Promise.all(
                                              item.attachments
                                                // Optional: Filter out inline attachments if needed
                                                // .filter(att => !att.isInline)
                                                .map(async (attachment) => {
                                                  console.log(`Fetching content for: ${attachment.name} (ID: ${attachment.id}, Type: ${attachment.attachmentType})`);
                                                  return new Promise((resolveAttachment, rejectAttachment) => {
                                                       // Request content as base64
                                                       const options = { asyncContext: attachment.name, coercionType: Office.CoercionType.Text }; // Or Base64 if available/needed? Test what works. Often text = base64 for files.
                                                        // IMPORTANT: Use item.getAttachmentContentAsync only for item attachments!
                                                       if (attachment.attachmentType === Office.MailboxEnums.AttachmentType.Item){
                                                          console.warn(`Skipping item attachment content fetch: ${attachment.name}`);
                                                          // Resolve with basic info for item attachments, content fetch not standard via this method
                                                          resolveAttachment({
                                                              id: attachment.id,
                                                              name: attachment.name,
                                                              contentType: "message/rfc822", // Approximate type
                                                              size: attachment.size,
                                                              isInline: attachment.isInline,
                                                              attachmentType: attachment.attachmentType,
                                                              content: null, // Content fetch needs different API/method
                                                              format: null,
                                                              error: "Item attachment content not fetched via getAttachmentContentAsync"
                                                          });
                                                           return; // Skip content fetch
                                                      }

                                                       // Use getAttachmentContentAsync for File attachments
                                                        item.getAttachmentContentAsync(attachment.id, { asyncContext: attachment.name }, (contentResult) => {
                                                           if (contentResult.status !== Office.AsyncResultStatus.Succeeded) {
                                                               console.error(`Failed content fetch for ${contentResult.asyncContext}:`, contentResult.error);
                                                              rejectAttachment(new Error(`Failed to get content for: ${contentResult.asyncContext}: ${contentResult.error?.message}`));
                                                           } else {
                                                               console.log(`Success content fetch for: ${contentResult.asyncContext}, Format: ${contentResult.value.format}`);
                                                              resolveAttachment({
                                                                   id: attachment.id,
                                                                   name: attachment.name,
                                                                   contentType: attachment.contentType,
                                                                   size: attachment.size,
                                                                   isInline: attachment.isInline,
                                                                    attachmentType: attachment.attachmentType,
                                                                   content: contentResult.value.content, // Usually base64 for files
                                                                   format: contentResult.value.format, // e.g., Office.MailboxEnums.AttachmentContentFormat.Base64
                                                                    error: null
                                                               });
                                                           }
                                                       });
                                                   });
                                               })
                                           );
                                           console.log("Step 4: Attachment content fetching complete.");
                                       } else {
                                           console.log("Step 4: No attachments found on the item.");
                                       }


                                      // 5. Convert the fetched email blob to base64.
                                      console.log("Step 5: Converting .eml blob to base64...");
                                      const emailContentBase64 = await blobToBase64(emailBlob);
                                      console.log("Step 5: .eml conversion complete.");

                                      // 6. Construct the data payload to send back to the React dialog.
                                      console.log("Step 6: Constructing emailData payload...");
                                       // Gather basic email properties safely
                                      const fromInfo = item.from || item.sender; // Prefer 'from', fallback to 'sender'
                                      const bodyPreviewPromise = new Promise((resolveBody) => {
                                          if (item.body && typeof item.body.getAsync === 'function') {
                                              item.body.getAsync(Office.CoercionType.Text, { asyncContext: "bodyPreview" }, (result) => {
                                                   if (result.status === Office.AsyncResultStatus.Succeeded) {
                                                       resolveBody(result.value.substring(0, 500)); // Limit preview size
                                                   } else {
                                                       console.warn("Could not get body preview:", result.error);
                                                       resolveBody("Could not get body preview.");
                                                   }
                                              });
                                           } else {
                                               resolveBody("Body preview not available.");
                                          }
                                       });

                                       const bodyPreview = await bodyPreviewPromise;


                                      const emailData:any = {
                                          itemId: item.itemId, // Original EWS ID
                                          restItemId: getItemRestId(), // REST formatted ID
                                          subject: item.subject || "",
                                          from: { // Send simplified 'from' info
                                              displayName: fromInfo?.displayName || "Unknown Sender",
                                              emailAddress: fromInfo?.emailAddress || "unknown@example.com",
                                           },
                                          toRecipients: item.to || [],
                                          ccRecipients: item.cc || [],
                                          bccRecipients: item.bcc || [], // Added Bcc
                                          bodyPreview: bodyPreview,
                                          attachments: attachmentsWithContent.filter(a => !a.error), // Filter out attachments with fetch errors
                                          emailContentBase64: emailContentBase64, // The full email content as base64
                                           errors: attachmentsWithContent.filter(a => a.error).map(a => a.error) // Include any attachment errors
                                      };

                                      console.log("Step 6: Payload constructed. Sending to dialog (logging structure only)...");
                                      // Avoid logging potentially huge base64 strings in production.
                                      const loggableData = {
                                           ...emailData,
                                          attachments: emailData.attachments.map(a => ({ ...a, content: a.content ? `[Base64 Content Present - Length: ${a.content.length}]` : "[No Content]"})),
                                          emailContentBase64: emailData.emailContentBase64 ? `[Base64 Content Present - Length: ${emailData.emailContentBase64.length}]` : "[No Content]"
                                      };
                                      console.log(JSON.stringify(loggableData, null, 2));

                                      // 7. Send the complete data payload back to the dialog.
                                      dialog.messageChild(JSON.stringify(emailData));
                                      console.log("Step 7: 'emailData' sent to dialog.");

                                   } catch (processingError) {
                                       console.error("Error processing data_request inside dialog handler:", processingError);
                                       // Send an error message back to the dialog to indicate failure.
                                        try {
                                           dialog.messageChild(JSON.stringify({ error: "Failed to prepare email data: " + processingError.message }));
                                       } catch(e) { console.error("Failed to send error message back to dialog", e); }
                                      // Optionally fail the main event if this data is critical
                                      // if(event && typeof event.completed === 'function'){ event.completed({ allowEvent: false, errorMessage:"Could not process email data."});}
                                   }
                              } else if (args.message === 'upload_success') {
                                   console.log("Received 'upload_success' from dialog. Allowing event.");
                                   try { if (dialog) dialog.close(); } catch (e) {}
                                    if(event && typeof event.completed === 'function'){
                                       event.completed({ allowEvent: true }); // Or true, if send should proceed
                                  }

                               } else if (args.message === 'operation_cancelled' || args.message === 'close_dialog') {
                                  console.log(`Received '${args.message}' from dialog. Stopping event.`);
                                   try { if (dialog) dialog.close(); } catch (e) {}
                                    if(event && typeof event.completed === 'function'){
                                        event.completed({ allowEvent: false, errorMessage: "Operation cancelled by user." });
                                   }
                               }
                               else {
                                   console.warn(`Received unknown message from dialog: ${args.message}`);
                               }
                          });

                          console.log("Step 3: Dialog handlers successfully added.");
                          resolveDialog(); // Resolve the promise once the dialog is open and handlers are set
                       } else {
                          console.error("Error opening dialog: Code=" + asyncResult.error.code + " Message=" + asyncResult.error.message);
                           rejectDialog(new Error("Could not open the add-in dialog: " + asyncResult.error.message));
                       }
                   }
              );
           });
          // If we reach here, the dialog is open and ready. Waiting for "data_request".

       } catch (error) {
           console.error("--- validateBeforeSend failed ---");
           console.error("Error during validateBeforeSend execution:", error);
           // Ensure the dialog is closed if it was opened
            try { if (dialog) dialog.close(); } catch (e) {}

           // IMPORTANT: Fail the Outlook event (e.g., stop the email from sending).
           if (event && typeof event.completed === 'function') {
               console.log("Completing event with allowEvent: false due to error.");
               event.completed({ allowEvent: false, errorMessage: `Add-in execution failed: ${error.message}` });
           } else {
               console.warn("Event object or event.completed function not available. Cannot signal failure to Outlook.");
           }
       }
  }

  // Register the main function with Office.js so Outlook knows which function to call.
  // The key "validateBeforeSend" should match the <Action xsi:type="ExecuteFunction"> <FunctionName> value in your manifest.xml.
  Office.actions.associate("validateBeforeSend", validateBeforeSend);
  console.log("Office.actions.associate('validateBeforeSend', validateBeforeSend) called.");

})(); // Self-executing function wrapper